.nav {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    flex-direction: column;
    gap: 30px;
    background-color: rgba(255, 255, 255, 0.4);
    padding: 10px 20px;
    z-index: 999;

    border-radius: 10px;

    /* gap: 30px ; */
}

.nav a {
    margin: 0 auto ;
    padding: 20px;
    background: rgba(255, 255, 255, 0.5);
    text-decoration: none;
    color: #0008fc;
    text-transform: uppercase;
    font-size: 16px;
    text-align: center;
    font-weight: 400;
    border-radius: 20px;
}
