* {
    box-sizing: border-box;
}
body {
    margin: 0;
    padding: 0;
    background: rgb(132, 110, 130);
    height: 100%;
}

html,
body,
#root,
.container {
    height: 100%;
    display: flex;
    justify-content: center;
}

.Loader {
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

@keyframes spin {
    0% {
        transform: rotate(0deg);
    }
    100% {
        transform: rotate(360deg);
    }
}

.Loader_text {
    width: 100px;
    height: 100px;
    border: 10px solid #f3f3f3;
    border-radius: 50%;
    border-top: 10px solid #715a71;
    animation: spin 2s linear infinite;
    display: flex;
    justify-content: center;
    align-items: center;
}

.movies {
    display: flex;
    flex-wrap: wrap;
    padding: 50px;
    padding-top: 70px;
    width: 100%;
}

.movies a {
    display: contents;
    text-decoration: none;
}

.movies .movie {
    overflow: hidden;
    width: 48%;
    height: 240px;
    background-color: rgba(255, 255, 255, 0.67);
    margin-bottom: 70px;
    /* display: flex;
    align-items: flex-start;
    justify-content: space-between; */
    font-weight: 300;
    padding: 24px;
    border-radius: 8px;
    color: #000;
    box-shadow: 2px 2px 32px rgba(0, 0, 0, 0.2);
}

.movie img {
    position: relative;
    top: 30px;
    max-width: 100px;
    margin-right: 30px;
    box-shadow: 0 30px 60px -12px rgba(50, 50, 93, 0.25), 0 18px 36px -18px rgba(0, 0, 0, 0.3),
        0 -12px 36px -8px rgba(0, 0, 0, 0.025);
}

.movie .movie_title,
.movie .year {
    margin: 0;
    font-weight: 500;
}

.movie .movie_title {
    margin-bottom: 5px;
    font-size: 24px;
    color: #2c2c2c;
}

.movie .genres {
    list-style: none;
    padding: 0;
    margin: 0;
    display: flex;
    margin: 5px 0px;
}

.movie .genres li {
    font-weight: 400;
    padding: 4px 8px;
    line-height: 1.4;
    border: 1px solid #000;
    border-radius: 4px;
    white-space: nowrap;
}

.movie .year {
    margin: 0 15px 16px 0;
    font-size: 14px;
    font-weight: 500;
}
